<template>
  <div id="app">
    <header>
      <h1><i class="fas fa-chart-line"></i> FxTurn Trade Fusion AI Basket Analysis</h1>
    </header>
    
    <main>
      <transition name="fade" mode="out-in">
        <basket-list 
          v-if="!selectedBasket" 
          :baskets="baskets"
          @view-basket="selectBasket"
          @update-baskets="updateBaskets" />
        <basket-detail
          v-else
          :basket="selectedBasket"
          @back="selectedBasket = null"
        />
      </transition>
    </main>

    <footer>
      <p>Powered by <a href="https://www.fxturn.com">FxTurn Trade Fusion AI</a></p>
    </footer>
  </div>
</template>

<script>
import BasketList from './components/BasketList.vue'
import BasketDetail from './components/BasketDetail.vue'

export default {
  name: 'App',
  components: {
    BasketList,
    BasketDetail
  },
  data() {
    return {
      selectedBasket: null,
      baskets: []
    }
  },
  created() {
    // Load baskets from localStorage when app starts
    const savedBaskets = localStorage.getItem('baskets')
    if (savedBaskets) {
      try {
        this.baskets = JSON.parse(savedBaskets)
      } catch (error) {
        console.error('Error loading baskets:', error)
      }
    }
  },
  methods: {
    selectBasket(basket) {
      this.selectedBasket = basket
    },
    updateBaskets(newBaskets) {
      this.baskets = newBaskets
      localStorage.setItem('baskets', JSON.stringify(newBaskets))
    }
  }
}
</script>

