import { createApp } from 'vue'
import App from './App.vue'
import axios from 'axios'
import './assets/main.css'

const app = createApp(App)

// Use production API URL from env, fallback to HTTPS backend
axios.defaults.baseURL = process.env.VUE_APP_API_URL || 'https://b.fxturn.com'
console.log('API Base URL:', axios.defaults.baseURL)  // <-- Debug log
axios.defaults.withCredentials = false
axios.defaults.timeout = 30000 // 30 seconds timeout
axios.defaults.headers.common = {
  'Accept': 'application/json',
  'Content-Type': 'application/json'
}

// Update request interceptor
axios.interceptors.request.use(config => {
  // Add cache prevention
  if (config.method === 'get') {
    config.params = {
      ...config.params,
      _t: Date.now()
    }
  }
  
  // Remove any CORS-related headers
  delete config.headers['Origin']
  delete config.headers['Access-Control-Allow-Origin']
  
  // Log all requests in development
  console.log('Request Config:', {
    url: config.url,
    method: config.method,
    headers: config.headers,
    withCredentials: config.withCredentials
  })
  return config
}, error => {
  console.error('Request configuration error:', error)
  return Promise.reject(error)
})

// Add request interceptor for debugging
axios.interceptors.request.use(config => {
  console.log('Making request:', {
    url: `${config.baseURL}${config.url}`,
    method: config.method,
    headers: config.headers,
    data: config.data
  })
  return config
})

// Add response interceptor for better error handling
axios.interceptors.response.use(response => {
  return response;
}, error => {
  console.error('Response Error:', error);
  if (error.response) {
    console.error('Status:', error.response.status);
    console.error('Data:', error.response.data);
  }
  return Promise.reject(error);
});

// Add error handling
app.config.errorHandler = (err, vm, info) => {
  console.error('Vue Error:', err)
  console.error('Component:', vm)
  console.error('Info:', info)
}

// Add warning handling
app.config.warnHandler = (msg, vm, trace) => {
  console.warn('Vue Warning:', msg)
  console.warn('Component:', vm)
  console.warn('Trace:', trace)
}

app.config.globalProperties.$axios = axios
app.mount('#app')

console.log('Vue app initialized')
