<template>
  <div class="basket-list">
    <div class="upload-zone"
         @drop.prevent="handleDrop"
         @dragover.prevent="handleDragOver"
         @dragenter.prevent
         @dragleave.prevent="handleDragLeave"
         :class="{ 'drag-active': isDragging }">
      <div class="upload-prompt">
        <i class="fas fa-cloud-upload-alt"></i>
        <h2>Drop Trading Files Here</h2>
        <p>or</p>
        <label class="file-input-label">
          <input type="file" @change="handleFileUpload" accept=".txt" multiple>
          Browse Files
        </label>
      </div>
    </div>

    <!-- Add loading overlay -->
    <div class="loading-overlay" v-if="uploading">
      <div class="loading-content">
        <i class="fas fa-spinner fa-spin"></i>
        <h3>Processing Files</h3>
        <p>{{ currentFile }}</p>
        <div class="progress-bar">
          <div class="progress" :style="{ width: progress + '%' }"></div>
        </div>
        <p class="progress-text">{{ progress }}% Complete</p>
      </div>
    </div>

    <div class="baskets-header" v-if="baskets.length > 0">
      <h2><i class="fas fa-chart-pie"></i> Trading Baskets ({{ baskets.length }})</h2>
      <button class="clear-all-btn" @click="clearAllBaskets">
        <i class="fas fa-trash-alt"></i> Clear All Baskets
      </button>
    </div>

    <div class="baskets-grid" v-if="baskets.length > 0">
      <div v-for="(basket, index) in sortedBaskets"
           :key="index"
           class="basket-card"
           :class="{ 'high-profit': isHighProfit(basket) }">
        <div class="basket-header">
          <h3>{{ basket.name }}</h3>
          <button class="delete-btn" @click="removeBasket(index)">
            <i class="fas fa-times"></i>
          </button>
        </div>
        <div class="basket-stats">
          <div class="stat">
            <span class="label">Basket Time:</span>
            <span class="value">{{ basket.date }}</span>
          </div>
          <div class="stat">
            <span class="label">Current Profit:</span>
            <span :class="['value', getProfitClass(basket.info['Current Profit'])]">
              {{ basket.info['Current Profit'] }}
            </span>
          </div>
          <div class="stat">
            <span class="label">Max Profit:</span>
            <span class="value">{{ basket.info['Max Profit'] }}</span>
          </div>
          <div class="stat">
            <span class="label">Max Drawdown:</span>
            <span class="value negative">{{ basket.info['Max Drawdown'] }}</span>
          </div>
          <div class="stat">
            <span class="label">Trades:</span>
            <span class="value">{{ basket.info['Number of Trades'] }}</span>
          </div>
        </div>
        <button class="view-btn" @click="viewBasketDetails(basket)">
          View Details
        </button>
      </div>
    </div>

    <div class="no-baskets" v-else>
      <i class="fas fa-inbox"></i>
      <p>No baskets uploaded yet. Drop your trading files here to start.</p>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import { formatDate } from '../utils/date-utils'

export default {
  name: 'BasketList',
  props: {
    baskets: {
      type: Array,
      required: true
    }
  },
  data() {
    return {
      isDragging: false,
      uploading: false,
      progress: 0,
      currentFile: '',
      totalFiles: 0,
      processedFiles: 0
    }
  },
  computed: {
    sortedBaskets() {
      return [...this.baskets].sort((a, b) => {
        const dateA = new Date(a.date.replace(' ', 'T'));
        const dateB = new Date(b.date.replace(' ', 'T'));
        return dateB - dateA; // Sort in descending order (newest first)
      });
    }
  },
  methods: {
    parseDateFromBasketName(name) {
      const match = name.match(/(\d{4})(\d{2})(\d{2})(\d{2})(\d{2})/);
      if (match) {
        const [_, year, month, day, hour, minute] = match;
        return `${year}-${month}-${day} ${hour}:${minute}`;
      }
      return null;
    },
    
    async handleFileUpload(event) {
      console.log('File upload triggered', event.target.files)
      if (event.target.files.length === 0) {
        console.log('No files selected')
        return
      }
      await this.processFiles(event.target.files)
    },
    async handleDrop(event) {
      console.log('File drop triggered', event.dataTransfer.files)
      event.preventDefault()
      if (event.dataTransfer.files.length === 0) {
        console.log('No files dropped')
        return
      }
      await this.processFiles(event.dataTransfer.files)
    },
    async processFiles(files) {
      this.uploading = true
      this.totalFiles = files.length
      this.processedFiles = 0
      const updatedBaskets = [...this.baskets]

      for (let file of files) {
        if (!file.name.endsWith('.txt')) continue
        
        this.currentFile = `Processing ${file.name}`
        this.progress = Math.round((this.processedFiles / this.totalFiles) * 100)

        const formData = new FormData()
        formData.append('file', file)
        
        try {
          const response = await axios.post('/upload/', formData, {
            headers: { 'Content-Type': 'multipart/form-data' },
            withCredentials: false,
            onUploadProgress: (progressEvent) => {
              const uploadProgress = Math.round(
                (progressEvent.loaded / progressEvent.total) * 100
              )
              this.progress = Math.round(
                (this.processedFiles * 100 + uploadProgress) / this.totalFiles
              )
            }
          })
          
          const basketName = file.name.replace('.txt', '');
          const basket = {
            name: basketName,
            info: response.data.info || {},
            charts: {
              profit: `${axios.defaults.baseURL}${response.data.chart_urls.profit}`,
              pairs: `${axios.defaults.baseURL}${response.data.chart_urls.pairs}`
            },
            date: this.parseDateFromBasketName(basketName) || '2025-01-01 00:00'
          }
          
          updatedBaskets.push(basket)
          this.processedFiles++
          
        } catch (error) {
          console.error('Upload failed:', error)
        }
      }
      
      this.progress = 100
      setTimeout(() => {
        this.uploading = false
        this.progress = 0
        this.currentFile = ''
        this.$emit('update-baskets', updatedBaskets)
      }, 500)
    },
    removeBasket(index) {
      const updatedBaskets = [...this.baskets]
      updatedBaskets.splice(index, 1)
      this.$emit('update-baskets', updatedBaskets)
    },
    getProfitClass(profit) {
      return profit >= 0 ? 'positive' : 'negative'
    },
    viewBasketDetails(basket) {
      this.$emit('view-basket', basket)
    },
    handleDragOver() {
      this.isDragging = true
    },
    handleDragLeave() {
      this.isDragging = false
    },
    clearAllBaskets() {
      if (confirm('Are you sure you want to remove all baskets?')) {
        this.$emit('update-baskets', [])
      }
    },
    isHighProfit(basket) {
      return parseFloat(basket.info['Max Profit']) > 100;
    }
  }
}
</script>

