<template>
  <div class="basket-detail">
    <div class="detail-header">
      <button class="back-btn" @click="$emit('back')">
        <i class="fas fa-arrow-left"></i> Back
      </button>
      <h2>{{ basket.name }}</h2>
    </div>

    <div class="charts-container">
      <div class="chart-card" v-if="basket.charts.profit">
        <img :src="basket.charts.profit" 
             alt="Profit Chart" 
             class="chart-image"
             @error="handleImageError"/>
      </div>
      <div class="chart-card" v-if="basket.charts.pairs">
        <img :src="basket.charts.pairs" 
             alt="Pairs Chart" 
             class="chart-image"
             @error="handleImageError"/>
      </div>
    </div>

    <div class="stats-grid">
      <div v-for="(value, key) in basket.info" 
           :key="key"
           class="stat-card"
           :class="{ 'profit-card': key.includes('Profit') }">
        <div class="stat-icon">
          <i :class="getIconForStat(key)"></i>
        </div>
        <p :class="getProfitClass(key, value)">{{ value }}</p>
        <small>{{ key }}</small>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'BasketDetail',
  props: {
    basket: {
      type: Object,
      required: true
    }
  },
  methods: {
    getIconForStat(key) {
      const icons = {
        'Current Profit': 'fas fa-dollar-sign',
        'Max Profit': 'fas fa-arrow-up',
        'Max Drawdown': 'fas fa-arrow-down',
        'Open Basket Time': 'fas fa-clock',
        'Number of Trades': 'fas fa-exchange-alt',
        'default': 'fas fa-chart-pie'
      };
      return icons[key] || icons.default;
    },
    getProfitClass(key, value) {
      if (!key.includes('Profit')) return '';
      const numValue = parseFloat(value.replace(/[^-\d.]/g, ''));
      return numValue >= 0 ? 'positive' : 'negative';
    },
    handleImageError(event) {
      console.error('Failed to load image:', event.target.src)
      event.target.style.display = 'none'
    }
  }
}
</script>

